@import url('reset.css');

/* Nav SCSS */

.tab-bar {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 6.4%;
	min-height: 54px;
	border-top: 1px solid #C1C1C1;
	background-color: #F7F7F7;
	ul {
		display: flex;
		flex-direction: row;
		margin-top: 10px;
		li {
				flex: 1;
				text-align: center;
				transition: transform 500ms linear 0s;
			&:hover {
				transform: scale(1.1);
			}

			.tab-link {
				color: #C0C1C2;
				i {
					display: block;
					font-size: 22px;
				}
				&.active {
					color: #513736;
				}
			}
		}//li
	}//ul
}//tab_bar